import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { finalize } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { plantModel } from '../../models/path/plantModel';

@Injectable({
  providedIn: 'root'
})
export class PlantService {

  context : BehaviorSubject<plantModel[]>;
  URL : string = environment.BASE_API_URI + "plant/";

  constructor(private http : HttpClient) {
    this.context = new BehaviorSubject<plantModel[]>(null);
    this.refreshContext();
   }


   refreshContext(){
     this.http.get<plantModel[]>(this.URL).subscribe( x => {
       this.context.next(x)
     });
   }

   update(model : plantModel){
     return this.http.put<plantModel[]>(this.URL, model)
     .pipe(finalize(() => { this.refreshContext(); }));
   }

   add(model : plantModel){
     return this.http.post<plantModel[]>(this.URL,model).pipe(finalize(() => {
      this.refreshContext()
    }))
   }

   deleteImage(id : number){
    return this.http.put(this.URL+"delete-img/"+id, null)
   }
   deleteAudio(id : number){
    return this.http.put(this.URL+"delete-audio/"+id, null)
   }
}